<template>
    <div class="h-full">
        <div class="hidden sm:block" style="width: 4rem"></div>
        <div class="fixed z-20" :class="!isExpanded ? ['hidden', 'sm:block'] : ''">
            <div class="bg-gray-800 h-screen text-white overflow-x-hidden"
                 :class="[isExpanded ? 'w-64' : 'w-16', transitionRunning]"
                 @mouseenter="onEnter"
                 @mouseleave="onLeave">
                <div class="p-4 py-1 flex items-center bg-gray-800 h-12 cursor-pointer">
                    <img src="../assets/scalaIcon.svg" alt="logo" class="h-8 w-8">
                    <span class="ml-4 uppercase font-bold relative whitespace-no-wrap" @click="$router.push({ name: 'home' })">Menu</span>
                </div>
                <ul class="mt-2 text-gray-500">
                    <li class="py-3 cursor-pointer hover:bg-gray-700 hover:text-white flex row-style" @click="$router.push({ name: 'historia' })">
                        <span v-show="isExpanded" class="relative ml-8 whitespace-no-wrap ">Historia</span>
                    </li>
                    <li class="py-3 cursor-pointer hover:bg-gray-700 hover:text-white flex row-style" @click="$router.push({ name: 'variables'  })">
                        <span v-show="isExpanded" class="relative ml-8 whitespace-no-wrap ">Variables</span>
                    </li>
                    <li class="py-3 cursor-pointer hover:bg-gray-700 hover:text-white flex row-style" @click="$router.push({ name: 'estructuras'  })">
                        <span v-show="isExpanded" class="relative ml-8 whitespace-no-wrap ">Estructuras </span>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        props: ['active'],
        data () {
            return {
                isExpanded: false,
                transitionRunning: null,
                disableMouseEvents: false
            }
        },
        methods: {
            onEnter () {
                if (this.disableMouseEvents) return
                this.disableMouseEventsMeanwhile(200)
                this.isExpanded = true
                this.transitionRunning = 'expand'
            },
            onLeave () {
                if (this.disableMouseEvents) return
                this.disableMouseEventsMeanwhile(200)
                this.isExpanded = false
                this.transitionRunning = 'contract'
            },
            close () {
                this.disableMouseEventsMeanwhile(200)
                this.isExpanded = false
                this.$emit('update:active')
                this.transitionRunning = 'contract'

            },
            disableMouseEventsMeanwhile(milliseconds) {
                this.disableMouseEvents = true
                setTimeout(() => this.disableMouseEvents = false, milliseconds)
            }
        },
        watch: {
            active (isActive) {
                this.transitionRunning = 'expand'
                this.isExpanded = isActive
                if (isActive) this.disableMouseEvents = true
            }
        }
    }
</script>

<style scoped>
    .expand {
        animation-name: expand;
        animation-duration: 200ms;
    }

    @keyframes expand {
        from {
            width: 4rem;
        }
        to {
            width: 16rem;
        }
    }

    .contract {
        animation-name: contract;
        animation-duration: 200ms;
    }

    @keyframes contract {
        from {
            width: 16rem;
        }
        to {
            width: 4rem;
        }
    }

    .row-style:hover{
        border-bottom: solid 1px slategray;
        border-top: solid 1px slategray;
    }
</style>