<template>
    <section class="text-gray-800 font-serif">
        <div class="body-style">
            <h1 class="title">Estructuras de control de flujo</h1>
            <div class="mt-12">
                <h2 class="subtitle">If / Else </h2>
                <h3 class="mt-8">La estructura de control if / else de Scala es similar a otros lenguajes: </h3>
                <div class="code-style bg-yellow-200 code-style rounded-lg mt-8">
                    <h3><span class="font-bold">if </span>(test1) {</h3>
                    <h3>doA()</h3>
                    <h3>} <span class="font-bold">else if</span> (test2) {</h3>
                    <h3>doB()</h3>
                    <h3>} <span class="font-bold">else if</span> (test3) {</h3>
                    <h3>doC()</h3>
                    <h3>} <span class="font-bold">else</span> {</h3>
                    <h3>doD()</h3>
                    <h3>}</h3>
                </div>
                <h3 class="mt-8">Sin embargo, a diferencia de Java y muchos otros lenguajes, la construcción if / else devuelve un valor, por lo que, entre otras cosas, puede usarlo como operador ternario:</h3>
                <div class="code-style bg-yellow-200 code-style rounded-lg mt-8">
                    <h3>val x = <span class="font-bold">if</span> (a &gt; b) a <span class="font-bold">else</span> b</h3>
                </div>
                <h3 class="subtitle mt-8">match expressions</h3>
                <h3 class="mt-8">Scala tiene una matchexpresión, que en su uso más básico es como una switchdeclaración de Java :</h3>
                <div class="code-style bg-yellow-200 code-style rounded-lg mt-8">
                 <h3>val result = i <span class="font-bold">match</span> {</h3>
                     <h3><span class="font-bold">case</span> 1 => "one"</h3>
                     <h3><span class="font-bold">case</span> 2 => "two"</h3>
                     <h3><span class="font-bold">case</span> _ => "not 1 or 2"</h3>
                     <h3>}</h3>
                </div>
                <h3 class="mt-8">La matchexpresión no se limita solo a enteros, se puede usar con cualquier tipo de datos, incluidos los booleanos. Aquí hay un ejemplo de matchser utilizado como el cuerpo de un método, y compararlo con muchos tipos diferentes:</h3>
                <div class="code-style bg-yellow-200 code-style rounded-lg mt-8 text-center">
                    <h3>def getClassAsString(x: Any):String = x <span class="font-bold">match</span> {</h3>
                        <h3> <span class="font-bold">case</span> s: String => s + " is a String"</h3>
                        <h3> <span class="font-bold">case</span> i: Int => "Int"</h3>
                       <h3>  <span class="font-bold">case</span> f: Float => "Float"</h3>
                       <h3>  <span class="font-bold">case</span> l: List[_] => "List"</h3>
                       <h3>  <span class="font-bold">case</span> p: Person => "Person"</h3>
                        <h3> <span class="font-bold">case</span> _ => "Unknown"</h3>
                      <h3>  }</h3>
                </div>
                <h3 class="subtitle mt-8">try/catch</h3>
                <h3 class="mt-8">La estructura de control try / catch de Scala le permite capturar excepciones. Es similar a Java, pero su sintaxis es consistente con las expresiones de coincidencia:</h3>
                <div class="code-style bg-yellow-200 code-style rounded-lg mt-8 ">
                   <h3><span class="font-bold">try</span> {</h3>
                    <h3>writeToFile(text)</h3>
                    <h3>} <span class="font-bold">catch</span> {</h3>
                    <h3><span class="font-bold">case</span> fnfe: FileNotFoundException => println(fnfe)</h3>
                    <h3><span class="font-bold">case</span> ioe: IOException => println(ioe)</h3>
                    <h3>}</h3>
                </div>
                <h3 class="subtitle mt-8">for loops and expressions</h3>
                <h3 class="mt-8">Los forbucles Scala , que generalmente escribimos en este libro como bucles for, se ven así:</h3>
                <div class="code-style bg-yellow-200 code-style rounded-lg mt-8 ">
                    <h3><span class="font-bold">for</span> (arg &lt;- args) println(arg)</h3>
                    <h3><span class="font-bold">for</span> (i &lt;- 0 to 5) println(i)</h3>
                    <h3><span class="font-bold">for</span> (i &lt;- 0 to 10 by 2) println(i)</h3>
                </div>
                <h3 class="subtitle mt-8">while y do / while</h3>
                <h3 class="mt-8">Scala también tiene whiley do/ whilebucles. Aquí está su sintaxis general:</h3>
                <h3>// while loop
                    while(condition) {
                    statement(a)
                    statement(b)
                    }

                    // do-while
                    do {
                    statement(a)
                    statement(b)
                    }
                    while(condition)</h3>

            </div>
        </div>
    </section>
</template>

<script>
    export default {
        name: "Estructuras"
    }
</script>

