<template>
  <section class="text-gray-800 font-serif ">
      <div class="body-style ">
          <h1 class="title">Variables</h1>
          <div class="text-style mt-8">
              <h2 class="subtitle ">Declaración de variables</h2>
              <h3 class="mt-12">Scala tiene una sintaxis diferente para declarar variables. Se pueden definir como valor, es decir, constante o variable.</h3>
              <ul class="flex mt-8 ">
                  <li class="mx-4 bg-gray-200 rounded-lg">
                      <h2 class="font-bold mt-4">Variable mutable</h2>
                      <h3 class="mb-2 text-left mt-4">Para myVar se declara usando la palabra clave var. Es una variable que puede cambiar el valor y esto se llama variable mutable . La siguiente es la sintaxis para definir una variable usando la palabra clave var :</h3>
                      <h3 class="bg-yellow-200 code-style rounded-lg mt-4">var myVar : String = "Foo"</h3>
                  </li>
                  <li class="mx-4 bg-gray-200 rounded-lg">
                      <h2 class="font-bold mt-4">Variable inmutable</h2>
                      <h3 class="mb-2 text-left mt-4">Para myVal se declara utilizando la palabra clave val. Esto significa que es una variable que no se puede cambiar y esto se llama variable inmutable . La siguiente es la sintaxis para definir una variable usando la palabra clave val:</h3>
                      <h3 class="bg-yellow-200 code-style rounded-lg mt-4">val myVal : String = "Foo"</h3>
                  </li>
              </ul>
              <h2 class="subtitle mt-12">Inferencia de tipo variable</h2>
              <h3 class="mb-8 text-center mt-12"> En Scala, normalmente crea variables sin declarar su tipo:</h3>
              <div class="bg-yellow-200 code-style rounded-lg mt-4">
                  <h3 class="text-center">val x = 1</h3>
                  <h3 class="text-center">val s = "a string"</h3>
                  <h3 class="text-center">val p = new Person("Regina")</h3>
              </div>
              <h3 class="mb-2 text-center mt-8">Esta característica se conoce como inferencia de tipos , y es una excelente manera de ayudar a mantener su código conciso. También puede declarar explícitamente el tipo de una variable, pero eso no suele ser necesario:</h3>
              <div class="bg-yellow-200 code-style rounded-lg mt-8">
                  <h3>val x: Int = 1</h3>
                  <h3>val s: String = "a string"</h3>
                  <h3>val p: Person = new Person("Regina")</h3>
              </div>
              <h2 class="subtitle mt-12 mb-12">Alcance de las variables</h2>
              <h3 class="text-style ">Las variables en Scala pueden tener tres ámbitos diferentes según el lugar donde se utilizan. Pueden existir como campos, como parámetros de método y como variables locales. A continuación se muestran los detalles sobre cada tipo de alcance.</h3>
              <div class="text-style mt-8 ">
                  <h3 class="font-bold text-left">Campos :</h3>
                  <h3 class="mt-8 ">Los campos son variables que pertenecen a un objeto. Se puede acceder a los campos desde todos los métodos del objeto. También se puede acceder a los campos fuera del objeto, según los modificadores de acceso con los que se declare el campo. Los campos de objeto pueden ser de tipo mutable e inmutable y pueden definirse usando var o val .</h3>
                  <h3 class="font-bold mt-8 text-left">Parámetros del método :</h3>
                  <h3 class="mt-8 ">Los parámetros del método son variables, que se utilizan para pasar el valor dentro de un método, cuando se llama al método. Los parámetros del método solo son accesibles desde el interior del método, pero los objetos pasados pueden ser accesibles desde el exterior, si tiene una referencia al objeto desde fuera del método. Los parámetros del método son siempre inmutables, definidos por la palabra clave val.</h3>
                  <h3 class="font-bold mt-8 text-left">Variables locales : </h3>
                  <h3 class="mt-8 ">Las variables locales son variables declaradas dentro de un método. Las variables locales solo son accesibles desde el interior del método, pero los objetos que cree pueden escapar del método si los devuelve desde el método. Las variables locales pueden ser de tipo mutable e inmutable y pueden definirse usando var o val .</h3>
              </div>
          </div>
      </div>


  </section>

