<template>
  <div id="app" class="h-full flex">
    <sidebar :active.sync="isSidebarExpanded"/>
    <div class="flex-1">
      <navbar @expand:sidebar="isSidebarExpanded = !isSidebarExpanded"/>
      <div class="bg-fixed p-10 py-4 text-gray-800 min-h-screen router-style mb-8">
        <router-view />
      </div>
    </div>

  </div>
</template>

<script>

import navbar from './components/Navbar.vue'
import sidebar from './components/Sidebar'

export default {
  components: {sidebar, navbar},
  data() {
    return {
      isSidebarExpanded: false
    }
  },
  methods: {
    expandSidebar() {
      this.isSidebarHover = true
      setTimeout(() => {
        if (this.isSidebarHover) {
          this.showSidebarText = true
        }
      }, 100);
    },
    contractSidebar() {
      this.isSidebarHover = false
      this.showSidebarText = false
    }
  }
}
</script>

